html, button {
  color: hsl(0, 0%, 5%);
  background: hsl(0, 0%, 99%);
}

body {
  font: 500 calc(1.25em + .25vmin)/1 "Alumni Sans", Helvetica, sans-serif;
  letter-spacing: .01em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
  text-size-adjust: none; /* does autoprefixer add vendor prefix? */
}

body, menu, button {
  margin: 0;
}

#root > header, h1, h1 abbr, #nav-bar menu, label, label div {
  display: flex;
}

#root > header, h1, label {
  flex-flow: column;
}

#root > header, label, label div {
  align-items: center;
}

#root > header {
  min-height: calc(100vh - 2px);
}

h1 {
  justify-content: center;
  align-items: center;
  font-size: calc(1.25em + 1vmin);
  font-weight: 800;
  letter-spacing: normal;
  color: yellowgreen;
  background: #000;
  width: 3.4em;
  height: 3.4em;
  padding: .75em;
  border-radius: 50%;
  margin: 10vh 0 5vh;
  /* aspect-ratio: 1; */
}

[lang="en"], h2 {
  font-style: italic;
}

h1 div {
  margin-bottom: -.5em;
}

h1 abbr, label /* .competitions label */ {
  justify-content: space-between;
}

h1 abbr {
  font-size: 2.7em;
}

h1 abbr, [type="radio"]:checked ~ svg, :checked ~ span {
  color: #fff;
}

h1 abbr {
  font-weight: 900;
}

abbr {
  /* avoid content to be styled in small caps (on a few browers) */
  font-variant: none;
  text-decoration: none; /* and underlined */
}

/* h1 span {
  position: relative;
  right: .25rem;
} */

header > p {
  font-size: calc(1em + .5vmin);
}

h1 + p {
  letter-spacing: .02em;
  line-height: 1.25;
  padding: .5em .25em;
  margin: 0 10%;
  text-align: center;
}

/* h1 + p //, label {
  padding: .5em;
} */

h1 + p, h2 {
  border-top: solid 2px hsl(80, 61%, 30%);
  border-bottom: solid 2px hsl(80, 61%, 30%);
}

h1 ~ p:last-of-type {
  margin: 20vh 10%;
}

header > section {
  align-self: stretch;
  order: -1;
}

menu {
  padding: 0;
  list-style: none;
}

#nav-bar {
  height: calc(30px + 1.05em); /* same as its menu */
}

#nav-bar menu {
  justify-content: space-evenly;
  position: fixed;
  z-index: 1; /* cover the svg shirts */
  background: yellowgreen;
  width: 100%;
  overflow: scroll;
  box-shadow: 0 0 5px #000;
}

#nav-bar menu::-webkit-scrollbar { /* non-standard */
  display: none; /* avoid showing on windows */
}

#nav-bar label {
  font-size: .7em;
  color: #000;
  padding: .25em;
}

label, button {
  font-weight: 600;
}

[type="radio"], [type="checkbox"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

#nav-bar svg {
  width: 30px;
  height: 30px;
}

svg + svg, :checked ~ svg:not(.search), #nav-bar ~ * {
  display: none;
}

:checked ~ svg:last-of-type {
  display: block;
}

h2 {
  font-size: 1.5em; /* calc(1.25em + .5vmin); */
  font-weight: 800;
  letter-spacing: .04em;
  color: #000;
  padding: .25em;
  margin: calc(1em + 3vh) 0; /* calc(1em + 2vh)? */
}

.competitions > menu {
  /* font-size: calc(.875em + .5vmin); */
  letter-spacing: .02em;
  background: hsl(80, 61%, 80%);
  border-radius: 5px;
}

.competitions > menu, .competitions aside {
  width: 80%;
  max-width: 50ch;
}

.competitions label {
  flex-flow: wrap;
  padding: .4em;
}

.competitions img {
  /* background: #fff; */
  width: 1.4em;
  height: 1.4em;
  padding: .2em;
  border-radius: 50%;
  margin-right: .4em;
}

.competitions :checked + div img {
  background: #fff;
}

.competitions img[alt*="Angra"] {
  width: 1.2em;
  height: 1.2em;
  padding: .3em;
}

.competitions svg {
  width: 24px;
  height: 24px;
}

.competitions menu menu {
  display: none;
  background: hsl(80, 61%, 95%);
}

.competitions li:last-child menu,
.competitions li:last-child li:last-child button {
  border-radius: 0 0 5px 5px;
}

button {
  font-family: "Alumni Sans", Helvetica, sans-serif;
  font-size: 1em;
  line-height: 1;
  background: transparent;
  min-width: 100%;
  padding: .8em .4em .8em 2.6em; /* 2.6em = .4 + .2 + 1.4 + .2 + .4 */
  border: none;
  text-align: left;
}

.competitions button {
  letter-spacing: .02em;
}

button:disabled, aside {
  color: hsl(0, 0%, 60%);
}

button:disabled::after, .competitions aside p::before {
  content: "* ";
  vertical-align: top;
}

button:disabled::after {
  font-size: .7em;
}

button[class="acores"] span {
  font-style: italic;
}

.competitions aside p::before {
  vertical-align: text-top;
}

.competitions aside {
  visibility: hidden; /* TEMP: to be changed to display: none; */
  margin: calc(1.5em + 3vh); /* same as h2 */
}

aside p {
  font-size: .7em;
  font-weight: 400;
  letter-spacing: .02em;
  /* line-height: 1.2; */
  margin: 0;
}

