@media (hover: hover) {
  #nav-bar label:hover {
    color: #fff;
  }
  label:hover, label :hover, button:not(:disabled):hover {
    cursor: pointer;
  }
  .competitions label:hover {
    background: hsl(80, 61%, 85%);
  }
  .competitions li:first-child label:hover {
    border-radius: 5px 5px 0 0;
  }
  .competitions label:hover img {
    background: #fff;
  }
  /* .competitions li:last-child label:hover { TEMP
    border-radius: 0 0 5px 5px;
  } */
  button:not(:disabled):hover {
    color: hsl(80, 61%, 30%);
    /* background: #fff; */
  }
  /* button:disabled:hover { TEMP
    background: transparent;
    cursor: initial;
  } */
  article menu label:hover {
    color: hsl(0, 0%, 99%);
  }
  .team header a:hover {
    color: #fff;
    background: hsl(80, 61%, 60%);
  }
  li a:hover {
    color: hsl(80, 61%, 40%);
  }
  div > div section:last-child svg:hover {
    color: hsl(80, 61%, 40%);
  }
  tbody tr:hover {
    color: #000;
    background: hsl(80, 61%, 70%);
    cursor: pointer;
  }
  table + aside a:hover {
    background: hsl(80, 61%, 60%);
    border-color: hsl(80, 61%, 60%);
  }
  footer a:hover {
    color: yellowgreen;
  }
}

